<template>
  <div>
    <div id="map"></div>
    <div class="centerAddr" @click="moveCommunity">
      <div id="centerAddr"></div>
      <div>   </div>
    </div>
    <div class="map_wrap">
      <div
        id="map"
        style="width: 100%; height: 100%; position: relative; overflow: hidden"
      ></div>
    </div>
  </div>
</template>

<script>
import { getAddrByCode } from "@/api/baseAddr";
import { mapState } from "vuex";
const memberStore = "memberStore";

export default {
  data() {
    return {
      map: null,
      //  ------------------------------
      geocoder: null,
      dongCode: "",
      //  --------------------------------
      overlays: [],
      items: [],
      //, 
      x: 128.598409092694,
      y: 35.8383244008836,
    };
  },
  computed: {
    ...mapState(memberStore, ["userInfo"]),
  },
  methods: {
    initMap() {
      var container = document.getElementById("map");
      var options = {
        center: new kakao.maps.LatLng(this.y, this.x),
        level: 3,
      };
      //  
      this.map = new kakao.maps.Map(container, options);

      this.moveMapCenter();

      //  ------------------------------
      this.geocoder = new kakao.maps.services.Geocoder();

      //         
      this.searchAddrFromCoords(this.map.getCenter(), this.displayCenterInfo);

      //               
      kakao.maps.event.addListener(this.map, "idle", () => {
        this.searchAddrFromCoords(this.map.getCenter(), this.displayCenterInfo);
      });
    },

    //  ------------------------------
    searchAddrFromCoords(coords, callback) {
      //     
      this.geocoder.coord2RegionCode(
        coords.getLng(),
        coords.getLat(),
        callback
      );
    },

    //        
    displayCenterInfo(result, status) {
      if (status === kakao.maps.services.Status.OK) {
        // console.log("centerAddr", result);
        var infoDiv = document.getElementById("centerAddr");

        for (var i = 0; i < result.length; i++) {
          if (result[i].region_type === "B") {
            infoDiv.innerHTML = result[i].address_name;
            this.dongCode = result[i].code;
            break;
          }
        }
      }
    },

    moveCommunity() {
      // console.log("called moveCommunity", this.dongCode);
      this.$router.push(`/community/list/${this.dongCode}`);
    },

    //  --------------------------------
    // 
    showOverlay(items, selsectedTab) {
      // console.log("called showOverlay");
      // console.log("items", items);
      if (items) {
        if (selsectedTab == 3) {
          this.y = items[0].y;
          this.x = items[0].x;
        } else {
          this.y = items[0].lat;
          this.x = items[0].lng;
        }
        // console.log(this.y, this.x);
        this.map.panTo(new kakao.maps.LatLng(this.y, this.x));
      }
      this.removeOverlay();
      // 
      items.forEach((item) => {
        let position, content;
        // 
        if (selsectedTab == "1") {
          position = new kakao.maps.LatLng(item.lat, item.lng);
          content = `<div class="label"><div>${item.apartmentName}</div><div>${item.dealAmount}</div></div>`;
        }
        // 
        else if (selsectedTab == "2") {
          position = new kakao.maps.LatLng(item.lat, item.lng);
          content = `<div class="label"><div>${item.storeName}</div></div>`;
        }
        // 
        else if (selsectedTab == "3") {
          position = new kakao.maps.LatLng(item.y, item.x);
          content = `<div class="label"><div>${item.place_name}</div></div>`;
        }
        //  
        let overlay = new kakao.maps.CustomOverlay({
          position: position, //   
          content: content,
        });
        this.overlays.push(overlay);
      });
      // console.log("overlays", this.overlays);
      this.setOverlay(this.map);
    },

    // 
    removeOverlay() {
      // console.log("called removeOverlay");
      this.setOverlay(null);
      this.overlays = [];
    },

    //   
    setOverlay(map) {
      for (var i = 0; i < this.overlays.length; i++) {
        this.overlays[i].setMap(map);
      }
    },

    //  ----------------------------------------
    moveMapCenter() {
      if (this.userInfo) {
        //       
        this.moveDongAddr(this.userInfo.dongCode);
      } else {
        //     
        this.moveLocation();
      }
    },

    //   
    moveDongAddr(dongCode) {
      // console.log("called moveDongAddr", dongCode);
      this.removeOverlay();
      getAddrByCode(dongCode, ({ data }) => {
        // console.log("address", data);
        let address = data.sidoName + data.gugunName + data.dongName;
        this.geocoder.addressSearch(address, (result, status) => {
          //   
          if (status === kakao.maps.services.Status.OK) {
            // console.log("addr search result", result);
            this.x = result[0].x;
            this.y = result[0].y;
            var coords = new kakao.maps.LatLng(this.y, this.x);

            //      
            this.map.setCenter(coords);
          }
        });
      });
    },

    //geolocation    
    moveLocation() {
      let locPosition = new kakao.maps.LatLng(33.450701, 126.570667);
      // console.log("navigator.geolocation", navigator.geolocation);
      // HTML5 geolocation    
      if (navigator.geolocation) {
        // GeoLocation    
        navigator.geolocation.getCurrentPosition((position) => {
          this.y = position.coords.latitude; // 
          this.x = position.coords.longitude; // 

          locPosition = new kakao.maps.LatLng(this.y, this.x);

          this.map.setCenter(locPosition);
        });
      }
    },
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&libraries=services&appkey=98a415ef4aeaea393f99197ba6f48da6";
      document.head.appendChild(script);
    }
  },
};
</script>

<style>
#map {
  width: 100%;
  height: 100vh;
}

.map_wrap,
.map_wrap * {
  margin: 0;
  padding: 0;
  font-family: "Malgun Gothic", dotum, "", sans-serif;
  font-size: 12px;
}
.map_wrap {
  position: absolute;
  width: auto;
  height: auto;
  left: 455px;
  top: 50px;
}

div.overlay {
  position: absolute;
  width: auto;
  height: auto;
  right: 5px;
  bottom: 70px;
  z-index: 3;
}

div.label {
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 0.5rem;
  font-size: 0.75rem;
  display: inline-block;
  padding: 2px 6px;
}

/*    */
div.centerAddr {
  position: absolute;
  width: auto;
  height: auto;
  right: 10px;
  top: 70px;
  z-index: 1;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  padding: 14px 20px;
  cursor: pointer;
  font-size: 0.9rem;
}

div#centerAddr {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 5px;
}
</style>
